wp.customize('osf_typography_general_body_font', function (value) {
    value.bind(function (to) {
        $('head').each((index, element) => {
            OSFPreviewCallback.reloadGoogleFont(element, to, '')
        });
        OSFPreviewSectionHover.getInstance().setPosition();
    });
});
